import { render } from '@testing-library/react';
import React, { Component } from 'react';
import pcCase from "./pc_case.jpg";
 class Case extends Component {
    render() {
      return (
        <div className="Case">
            <h1>Počítačová skříň</h1>
            <p> Základní parametry při výběru počítačové skříně jsou 
                velikost skříně, pro jaký typ desek je skříň určena a zda jsou v ní zabudované chladiče,
             nebo ne. Také se zaměřte na to, zda budete potřebovat něákou mechaniku CD, DVD, BluRay, nebo třeba čtečku SD karet. 
             Některé skříně na ni totiž nemají otvor.
             Doporučují se zaměřit třeba i na desing, jelikož se prodávájí skříně s prosklenou bočnicí, které se hodí, když si koupíte svíticí
             komponenty výsledek pak vypadá něák tako.</p>
             <img src={pcCase} alt="PC case" width="900" height="900"/>
             <p>U každé skříně máte napsanou velikost a podporované typy desek. Dávejte si pozor, aby jste si nevybrali něákou nekompatibilní. 
                Také se zaměřte na maximalní možnou velikost karet a maximální výšku Procesoru. (na této stránce nejsou sekce, protože výběr skříně se do nich nedá zařadit)
             </p>
        </div>
    )
}
}
export default Case