import { render } from '@testing-library/react';
import React, { Component } from 'react'



export  class motherboard extends Component {
    render() {
      return (
        <div className="Motherboard">
            <h1>Základní deska</h1>
            <p>Základní deska je komponent, do kterého se zapojí všechny součástky. Při jeho výběru je důležité ujasnit si jáké konektory potřebujete, 
                a jaký procesor si kupujete, kvůli typu socketu. Také se zaměřte na typ paměti RAM. Další na co by jste je počet a typ konektorů na disky a SSD.
            </p>
            <h3>Pro hráče</h3>
        U desky pro hráče je potřebné, aby podporovala DDR4 paměti ram ideálně s frekvencí alespoň 3000mhz. Další co se u ní může hodit je podpora SLI či 
        crossfire, což jsou prostředky k propojení dvou grafických karet. Důležitá je taky konektorová výbava. Také by jste si měli zjistit jaké disky do desk můžete zapojit.
            <h3>Pro programátory a střihače</h3>
                <p>Pokud jste programátor či střihač zaměřte se hlavně na to, aby deska podporovala vysokofrekvenční paměti RAM(3000mhz a více) ostatní je podobné jako v kolonce pro hráče. </p>
            <h3>Pro běžné uživatele</h3>
            <p>Pokud jste běžný uživatel nemusíte si dělat starosti s pamětmi ram můžete si třeba pořídit desku, která má i ddr3.
                 Doporučuji ale spíš paměti ddr4 s frekvencí kolem 2000mhz ta vám bude úplně stačit. Ohledně slotů na paměti ram postačí vám 2.
                   U konektorů vám bude stačit jen základní výbava.
                  </p>
        </div>
    )
}
}
export default motherboard