import { render } from '@testing-library/react';
import React, { Component } from 'react'



export  class disks extends Component {
    render() {
      return (
        <div className="disks">
            <h1>Disky</h1>
            <p>V počítači je důležité mít dostatek úložného prostoru, aby se vám do něj vešly soubory.
               Jsou dva typy pamětí. První je SSD, což
              je disk s menší životností, ale s větší rychlostí. Druhý je HDD, který je pomalejší než SSD, 
              má větší životnost a čím starší je tím je pomalejší.
              Jako velice dobrou cennově dostupnou variantu vnímám nainstalování systémů, a programů,
             u kterých potřebujete rychlé načítání na menší SSD a na HDD uložit méně náročné programy a soubory.
              Pokud jste už někdy dříve měli počítač či notebook víte, kolik místa potřebujete. Pokud ne doporučuji si koupit disky s 
              velikostí kolem 500gb. Také si dejte pozor na různé konektory disků většinou jsou konektory disku sata, ale nedavno byl vynalezen nový tip SSD disků s konektorem M.2.
            </p>
            <h3>Pro hráče</h3>
            <p>Vzhledem k tomu že jsou hry celkem velké doporučím paměť od 500 do 1000gb. Nejlepší je pokud budete mít všechno na SSD.
               Pokud ale máte napnutý rozpočet rozdělte své soubory na SSD a HDD.</p>
<h3>Pro programátory a střihače</h3>
<p>Zde bych doporučil podobný postup jako v kolonce pro hráče.</p>
<h3>Pro běžné uživatele</h3>
<p>Pro běžné uživatele doporučuji buď pouze HDD, nebo kombinaci s SSD. Bude vám stačit 500gb místa.</p>
        </div>
    )
}
}
export default disks