import { render } from '@testing-library/react';
import React, { Component } from 'react'



export  class RAM extends Component {
    render() {
      return (
        <div className="RAM">
            <h1>RAM</h1>
<p>Pameti ram jsou důležité na odkládání souborůů, které jsou rozpracované, ale v počítači nejsou uložené.
   (např. když píšete dokument neuložil/a jste si ho je uložen v paměti ram.) Jejich parametry jsou pměť a frekvence značená v mhz.
   Jejich druhy se značí ddr a číslo generace ramky ddr1, ddr2,ddr3 a ddr4 Pro hráče programátory a střihače doporučuji ddr4. Pro běžné uživatele postačí i ddr3, ale i přesto bych doporučil ddr4 klidně s nižší frekvencí.
</p>
            <h3>Pro hráče</h3>
<p>Pokud si pořizujete herní počítač doporučuji koupit si ramky s frekvencí mimnimálně 3000mhz a dohromady s pamětí alespoň 8gb. Lepší ale je, když mají 16gb</p>
<h3>Pro programátory a střihače</h3>
<a>Jelikož budete často rendrovat, je důležité, aby jste měli vysokofrekvenční ramky s pamětí 16gb a více.</a>
<h3>Pro běžné uživatele</h3>
<a>Pokud jste běžný uživatel bohatě vám postačí 4gb. Pokud ale chcete mít rychlejší počítač kupte si 8gb. Když budete mít 8gb počítač vám i déle vydrží a bude delší dobu použitelný.</a>
        </div>
    )
}
}
export default RAM