import { render } from '@testing-library/react';
import React, { Component } from 'react'



export  class system extends Component {
    render() {
      return (
        <div className="system">
            <h1>Operační systém</h1>
            <p>Do počítače si musíte sehnat operační systém. Buď si můžete koupit DVD s kompletním návodem a kódem na aktivaci, nebo si můžeete vitvořit
                instalační médium např z flashdisku. Návodů je nato spousta např.<a href="https://www.youtube.com/watch?v=9SP6y4UuFEo" target="blank">tento</a>
                Neaktivované windows sice můžete používat, ale časem bych doporučil koupit aktivační klíč. Pokud jste dříve měli počítač s aktivovaným windows
                můžete se poté přihlást stejným microsoft účtem a windows se vám aktivuje. 
            </p>
        </div>
    )
}
}
export default system