import { render } from '@testing-library/react';
import React, { Component } from 'react'



export  class gpu extends Component {
    render() {
      return (
        <div className="gpu">
            <h1>Grafická karta</h1>
            <a>Grafická karta zobrazuje všechno, co vydíte na obrazovce počítače. Parametrů grafických karet je opravdu hodně. 
            Nejdůležitějšími jsou pamět, počet stream procesorů a fekvence. Vzhledem k tomu že je parametrů opravdu hodně je lepší se koukat 
            na různé testy.(na internetu je najdete pod pojmem benchmark.) Nebo třeba na video, kde na grafické kartě testují něákou hru. základem pro výkonnější počítač je grafická karta s pamětí alespoň 2gb</a>
            <h3>Pro hráče</h3>
<p>Pokud chcete utáhnout nejnovější hry jako třeba Cyberpunk 2077, potřebujete kartu s pamětí alespoň 4gb jelikož je parametrů hodně doporučím 
  vyhledat si na youtube test hry, kterou chcete na počítači spustit. Základní herní grafickou kartu koupíte okolo 3 000.</p>
<h3>Pro programátory a střihače</h3>
Pro programátory a střihače se dělají specializované grafické karty. Takové seženete do pěti tisíc. Opět potřebujeme alespoň 4gb paměti.
<h3>Pro běžné uživatele</h3>
<p>Pokud jste běžný uživatel, doporučím pořídit si karu s 1 až 2 gb interní paměti. Ta vám vystačí na běžnou práci, i hraní starších her.</p>
        </div>

    )
}
}
export default gpu